// app/page.tsx
import { AppSidebar } from "@/components/app-sidebar"
import { SidebarProvider, SidebarInset, SidebarTrigger } from "@/components/ui/sidebar"
import { Separator } from "@/components/ui/separator"

import { TopBar } from "@/components/top-bar"
import RideCard from "@/components/ride-card"
import { prisma } from "@/lib/db"
import type { Ride } from "@/types/main"

// Make this a server component to fetch from Prisma
export default async function Home() {
  // Example: fetch 5 rides
  const fetchedRides: Ride[] = await prisma.ride.findMany({ take: 5 })

  return (
    <SidebarProvider>
      <AppSidebar />
      <SidebarInset>
        {/* Header area (optional) */}
        <header className="flex h-16 items-center gap-2 px-4 border-b">
          <SidebarTrigger className="-ml-1" />
          <Separator orientation="vertical" className="mr-2 h-4" />
          {/* You might show your brand name here or a small breadcrumb */}
          <h1 className="font-bold text-xl">Yideshare</h1>
        </header>

        {/* Content area */}
        <div className="flex flex-1 flex-col gap-4 p-4">
          {/* The top bar with from/to/date/time inputs + button */}
          <TopBar />

          {/* The actual list of rides */}
          <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3">
            {fetchedRides.length > 0 ? (
              fetchedRides.map((ride) => <RideCard key={ride.id} {...ride} />)
            ) : (
              <p>No rides available.</p>
            )}
          </div>
        </div>
      </SidebarInset>
    </SidebarProvider>
  )
}
