// app/components/ride-card.tsx
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "../../components/ui/card" // ✅ Correct path to shadcn components
import type { Ride } from "../types/main"

export default function RideCard(ride: Ride) {
  const { dateTime, beginning, destination, id } = ride

  // Format the date/time
  const month = dateTime.getMonth() + 1
  const day = dateTime.getDate()
  const year = dateTime.getFullYear()
  const hours = dateTime.getHours()
  const minutes = String(dateTime.getMinutes()).padStart(2, "0")

  return (
    <Card className="shadow-md">
      <CardHeader>
        <CardTitle>{beginning} → {destination}</CardTitle>
        <CardDescription>Ride ID: {id}</CardDescription>
      </CardHeader>
      <CardContent>
        <p className="text-sm text-muted-foreground">
          <strong>Date:</strong> {month}/{day}/{year}
        </p>
        <p className="text-sm text-muted-foreground">
          <strong>Time:</strong> {hours}:{minutes}
        </p>
      </CardContent>
    </Card>
  )
}
